<template>
  <div>
    <h3>Edit Item</h3>
    <div>
      <form @submit.prevent="updateItem" class="form-control">
        <input type="text" v-model="item_id" required class="form-control" />
        <label>Item Id</label>
        <input type="text" v-model="item_name" required class="form-control" />
        <label>Item Name</label>
        <input type="text" v-model="item_price" required class="form-control" />
        <label>Item Price</label>
        <br />
        <button type="submit" class="btn btn-primary">Submit</button>
        <button type="reset" @click="deleteItem" class="btn btn-danger">
          Delete
        </button>
        <router-link
          v-bind:to="{
            name: 'admin',
          }"
          class="btn btn-warning"
        >
          Cancel
        </router-link>
      </form>
    </div>
  </div>
</template>
<script>
import db from "@/firebaseConfig.js";
export default {
  name: "new-item",
  data() {
    return {
      item_id: null,
      item_name: null,
      item_price: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("categories")
      .doc(to.params.cat_id)
      .collection("items")
      .doc(to.params.item_id)
      .get()
      .then((doc) => {
        next((vm) => {
          vm.item_id = doc.data().item_id;
          vm.item_name = doc.data().item_name;
          vm.item_price = doc.data().item_price;
        });
      })
      .catch((err) => {
        console.log("Error getting documents", err);
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("categories")
        .doc(this.$route.params.cat_id)
        .collection("items")
        .doc(this.$route.params.item_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((item) => {
            this.item_id = item.data().item_id;
            this.item_name = item.data().item_name;
            this.item_price = item.data().item_price;
          });
        });
    },
    updateItem() {
      db.collection("categories")
        .doc(this.$route.params.cat_id)
        .collection("items")
        .doc(this.$route.params.item_id)
        .get()
        .then((item) => {
          item.ref
            .update({
              item_id: this.item_id,
              item_name: this.item_name,
              item_price: this.item_price,
            })
            .then((docRef) => this.$router.push("/admin"))
            .catch((error) => console.log(err));
        });
    },
    deleteItem() {
      if (confirm("Are you sure ?")) {
        db.collection("categories")
          .doc(this.$route.params.cat_id)
          .collection("items")
          .doc(this.$route.params.item_id)
          .get()
          .then((item) => {
            item.ref
              .delete()
              .then((docRef) => this.$router.push("/admin"))
              .catch((error) => console.log(err));
          });
      }
    },
  },
};
</script>
